import {component} from 'picoapp';
import gsap from 'gsap';

export default component((node, ctx) => {
	gsap.to(node, {
		y: () => window.innerHeight * 0.2,

		ease: 'none',

		scrollTrigger: {
			trigger: node,
			start: 'top top',
			end: 'bottom top',
			scrub: true,
		},
	});

	return () => {
		const trigger = gsap.getById(node);

		if (trigger) {
			trigger.kill();
		}
	};
});
